.main-nav {
  width: 100%;
}

.spacer {
  flex: 1 1 auto; /* empurra os botões para a direita */
}

.menu-image {
  margin: 20px 0;
  text-align: center;
}

.menu-image img {
  max-width: 80%; /* ajusta o tamanho da imagem */
  height: auto;
  border-radius: 10px; /* opcional: bordas arredondadas */
  box-shadow: 0px 4px 10px rgba(0,0,0,0.2); /* sombra leve */
}

.content {
  padding: 20px;
}
